import React, { useState } from "react";
import { Modal, Button, Form, Image } from "react-bootstrap";

function ProfileSettings({ show, handleClose }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const handleUpdate = async (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      alert("Passwords do not match");
      return;
    }

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/updateProfile`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ email, password }),
        }
      );

      const contentType = response.headers.get("content-type");
      if (!contentType || !contentType.includes("application/json")) {
        const text = await response.text();
        throw new Error(`Unexpected response type: ${text}`);
      }

      const data = await response.json();

      if (response.ok) {
        alert("Profile updated successfully");
        handleClose();
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error("Error:", error.message || error);
      alert("An error occurred. Please try again.");
    }
  };

  const handleDeleteAccount = async () => {
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/deleteAccount`,
        {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ email }),
        }
      );

      const contentType = response.headers.get("content-type");
      if (!contentType || !contentType.includes("application/json")) {
        const text = await response.text();
        throw new Error(`Unexpected response type: ${text}`);
      }

      const data = await response.json();

      if (response.ok) {
        alert("Account deleted successfully");
        handleClose();
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error("Error:", error.message || error);
      alert("An error occurred. Please try again.");
    }
  };

  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Profile Settings</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Image
          src={`${process.env.PUBLIC_URL}/images/profilepic.png`}
          roundedCircle
          className="mb-3"
        />
        <Form onSubmit={handleUpdate}>
          <Form.Group>
            <Form.Label>Email Address</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter new email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>New Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Enter new password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>Confirm New Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Confirm new password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
          </Form.Group>
          <Button variant="primary" type="submit" block>
            Update
          </Button>
          <Button variant="danger" onClick={handleDeleteAccount} block>
            Delete Account
          </Button>
        </Form>
      </Modal.Body>
    </Modal>
  );
}

export default ProfileSettings;
